import { Menu } from 'antd';
import { useState, useEffect } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import routes from '@/config/routes';
import { useAuth } from '@/contexts/AuthContext';

// ÈÄíÂΩíÂ§ÑÁêÜË∑ØÁî±ÈÖçÁΩÆÁîüÊàêËèúÂçïÈ°π
const generateMenuItems = (routes, parentPath = '') => {
  // const { hasPermission } = useAuth();
  
  return routes.reduce((items, route) => {
    // ËøáÊª§ÈöêËóèÈ°πÂíåÊùÉÈôê‰∏çË∂≥ÁöÑÈ°π
    // if (route.meta.hideInMenu || !hasPermission(route.roles)) return items;
    if (route.meta.hideInMenu) return items;
    
    const fullPath = `${parentPath}/${route.path}`.replace(/\/+/g, '/');
    
    const menuItem = {
      key: fullPath,
      label: route.meta.label,
      icon: route.meta.icon,
      children: route.children 
        ? generateMenuItems(route.children, fullPath)
        : undefined
    };

    return [...items, menuItem];
  }, []);
};

const LeftMenu = () => {
  const location = useLocation();
  const navigate = useNavigate();
  const [openKeys, setOpenKeys] = useState([]);
  const [selectedKeys, setSelectedKeys] = useState([]);

  // ÁîüÊàêËøáÊª§ÂêéÁöÑËèúÂçïÈ°π
  const menuItems = generateMenuItems(routes);

  // Ê†πÊçÆÂΩìÂâçË∑ØÂæÑËÆæÁΩÆÈÄâ‰∏≠Áä∂ÊÄÅ
  useEffect(() => {
    const path = location.pathname;
    console.log('üöÄ ~ useEffect ~ path:', path)
    setSelectedKeys([path]);
    
    // Ëá™Âä®Â±ïÂºÄÁà∂Á∫ßËèúÂçï
    const keys = path.split('/').filter(Boolean);
    const newOpenKeys = !['home'].includes(keys[0]) ? keys.slice(0, -1).map((_, index) => 
      `/${keys.slice(0, index + 1).join('/')}`
    ) : ['/'];

    setOpenKeys(newOpenKeys)
  }, [location]);

  // Â§ÑÁêÜËèúÂçïÁÇπÂáª
  const handleClick = ({ key }) => {
    navigate(key);
  };

  // Â§ÑÁêÜËèúÂçïÂ±ïÂºÄ
  const handleOpenChange = (keys) => {
    const latestOpenKey = keys.find(key => !openKeys.includes(key));
    setOpenKeys(latestOpenKey ? [latestOpenKey] : []);
  };

  return (
    <Menu
      mode="inline"
      // theme="dark"
      items={menuItems}
      openKeys={openKeys}
      selectedKeys={selectedKeys}
      onClick={handleClick}
      onOpenChange={handleOpenChange}
      style={{ height: '100%', borderRight: 0 }}
    />
  );
};

export default LeftMenu;
